import { Injectable } from '@angular/core';
import { Observable, of, BehaviorSubject } from 'rxjs';

import { HelpersComponent } from '../helpers/helpers.component';

import { Project, State } from '../interfaces/project';
import PROJECTS from '../mock-projects';

@Injectable({
  providedIn: 'root'
})
export class ProjectService {

  projects: Project[] = PROJECTS;
  projects$ = new BehaviorSubject<Project[]>(PROJECTS);
  state: State;
  state$ = new BehaviorSubject<State>({
    filterByStatus: 'all',
    filterByType: 'all',
    sortByAttr: 'year',
  });

  setState(state?: State): void {
    this.state = {
      ...this.state,
      ...state
    };
    this.filterProjects();
    this.sortProjects();
  }

  getState(): Observable<State> {
    return of(this.state);
  }

  getProjects(): Observable<Project[]> {
    return of(this.projects);
  }

  filterProjects() {
    this.projects$.next(
      HelpersComponent.filterBy(this.projects, 'type', this.state.filterByType)
      );
  }

  sortProjects() {
    this.projects$.next(
      HelpersComponent.orderBy(this.projects, ...[this.state.sortByAttr])
      );
  }
}
